
.leaf{
  position:relative;
  left:calc(50% - 60px);
}
.leaf .tomato-leaf {
   position:absolute;
   border-top-right-radius: 100%;
   border-bottom-left-radius: 100%;
   height: 55px;
     width: 20px;
      top: 10px;
      background:green;
}
.leaf .leaf1 {
  left:30px;
      -webkit-transform:rotate(-40deg);
              transform:rotate(-40deg);
}
.leaf .leaf2 {
  left: 60px;
  top:0px;
    -webkit-transform:rotate(50deg);
            transform:rotate(50deg);
}
.leaf .leaf3 {
  left: 70px;
    -webkit-transform:rotate(80deg);
            transform:rotate(80deg);
}
.leaf .peduncle{
    position:absolute;
      border-radius: 20%;
     height: 50px;
     left: 50px;
     top: 0;
     width: 10px;
  background:green;
}
.clock{
  position:relative;
  font-size:54px;
  margin:64px auto 40px;
  border:8px solid #ccc;
  height:200px;
  width:200px;
  border-radius: 109px;
  -webkit-box-shadow: 0 0 0 10px #ff6666;
          box-shadow: 0 0 0 10px #ff6666;
}
.pie{  
    top:0;
    left:0;
    position:absolute;  
    width:184px;  
    height:184px;  
    border-radius:92px;  
    clip: rect(0px, 92px, 184px, 0px);
    -webkit-transform:rotate(0deg);
            transform:rotate(0deg);
}
.bg {
     background:#F08080;
}
.zindex{
  z-index:1;
}
.clock .timenum{
  position:absolute;
  z-index:2;
  text-align: center;
  line-height:184px;
  left:25px;
}
.settime{
  margin-top:10px;
}
.timing input[type="checkbox"] {
    position: absolute;
    clip: rect(0,0,0,0);
}
.timechoose{
  margin:10px 10px;
}
.timechoose >input[type="number"]{
 width:50px;
 margin-right:10px;
}

